<template>
  <div>
    <div class="animated fadeInDown">
      <img src="/static/img/it/banner.jpg" alt="">
    </div>
    <div class="it">
      <div class="nav">
        <router-link to="/it" :class="activeClass == 0 ? 'active':''">全部</router-link>
        <router-link to="/it?sm_class=1" :class="activeClass == 1 ? 'active':''">css</router-link>
        <router-link to="/it?sm_class=2" :class="activeClass == 2 ? 'active':''">jquery</router-link>
        <router-link to="/it?sm_class=3" :class="activeClass == 3 ? 'active':''">vue-cil</router-link>
        <router-link to="/it?sm_class=4" :class="activeClass == 4 ? 'active':''">demo</router-link>
        <router-link to="/it?sm_class=5" :class="activeClass == 5 ? 'active':''">animate</router-link>
      </div>
      <div class="it-container">
        <div class="it-nav animated fadeInLeft">
          <div>
            <p class="nav-title">常见问题</p>
            <router-link to="">全部</router-link>
            <router-link to="">css</router-link>
            <router-link to="">jquery</router-link>
            <router-link to="">vue-cil</router-link>
            <router-link to="">demo</router-link>
            <router-link to="">animate</router-link>
            <router-link to="">全部</router-link>
            <router-link to="">css</router-link>
            <router-link to="">jquery</router-link>
            <router-link to="">vue-cil</router-link>
            <router-link to="">demo</router-link>
            <router-link to="">animate</router-link>
            <router-link to="">全部</router-link>
            <router-link to="">css</router-link>
            <router-link to="">jquery</router-link>
            <router-link to="">vue-cil</router-link>
            <router-link to="">demo</router-link>
            <router-link to="">animate</router-link>
            <router-link to="">全部</router-link>
            <router-link to="">css</router-link>
            <router-link to="">jquery</router-link>
            <router-link to="">vue-cil</router-link>
            <router-link to="">demo</router-link>
            <router-link to="">animate</router-link>
          </div>
        </div>
        <div class="it-content">
          <it_article class="animated fadeInDown" :deta="!transfer_article  ? article : transfer_article" v-if="isRouterAlive"></it_article>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 父组通过  :deta="data中的数据"  的方式将数据传递给子组件
//import myHead from "./head.vue"
import it_article from "@/components/basis/it_article"
export default {
   components: {
     it_article
  },
  data() {
    return {
      isRouterAlive:true,
      activeClass:0,
      article:[
        {id:1,sm_class:'1',title:'彩虹之国-南非1',time:'2019-10-25 15:14',intro:'来到2019年，持续第六年的鳗荒也不见好转。由于暖流变化“黑潮大蛇行”影响了鳗鱼苗的回归时间，再加上长久以来的过度捕捞，根据日本水产厅的数据，今年渔期的鳗苗捕获量只有3.7吨，是2003年以来的最低水平。'},
        {id:2,sm_class:'2',title:'彩虹之国-南非2',time:'2019-10-25 15:14',intro:'来到2019年，持续第六年的鳗荒也不见好转。由于暖流变化“黑潮大蛇行”影响了鳗鱼苗的回归时间，再加上长久以来的过度捕捞，根据日本水产厅的数据，今年渔期的鳗苗捕获量只有3.7吨，是2003年以来的最低水平。'},
        {id:3,sm_class:'3',title:'彩虹之国-南非3',time:'2019-10-25 15:14',intro:'来到2019年，持续第六年的鳗荒也不见好转。由于暖流变化“黑潮大蛇行”影响了鳗鱼苗的回归时间，再加上长久以来的过度捕捞，根据日本水产厅的数据，今年渔期的鳗苗捕获量只有3.7吨，是2003年以来的最低水平。'},
        {id:4,sm_class:'4',title:'彩虹之国-南非4',time:'2019-10-25 15:14',intro:'来到2019年，持续第六年的鳗荒也不见好转。由于暖流变化“黑潮大蛇行”影响了鳗鱼苗的回归时间，再加上长久以来的过度捕捞，根据日本水产厅的数据，今年渔期的鳗苗捕获量只有3.7吨，是2003年以来的最低水平。'},
        {id:5,sm_class:'5',title:'彩虹之国-南非5',time:'2019-10-25 15:14',intro:'来到2019年，持续第六年的鳗荒也不见好转。由于暖流变化“黑潮大蛇行”影响了鳗鱼苗的回归时间，再加上长久以来的过度捕捞，根据日本水产厅的数据，今年渔期的鳗苗捕获量只有3.7吨，是2003年以来的最低水平。'},
        {id:6,sm_class:'1',title:'彩虹之国-南非6',time:'2019-10-25 15:14',intro:'来到2019年，持续第六年的鳗荒也不见好转。由于暖流变化“黑潮大蛇行”影响了鳗鱼苗的回归时间，再加上长久以来的过度捕捞，根据日本水产厅的数据，今年渔期的鳗苗捕获量只有3.7吨，是2003年以来的最低水平。'},
        {id:7,sm_class:'2',title:'彩虹之国-南非7',time:'2019-10-25 15:14',intro:'来到2019年，持续第六年的鳗荒也不见好转。由于暖流变化“黑潮大蛇行”影响了鳗鱼苗的回归时间，再加上长久以来的过度捕捞，根据日本水产厅的数据，今年渔期的鳗苗捕获量只有3.7吨，是2003年以来的最低水平。'},
        {id:8,sm_class:'3',title:'彩虹之国-南非8',time:'2019-10-25 15:14',intro:'来到2019年，持续第六年的鳗荒也不见好转。由于暖流变化“黑潮大蛇行”影响了鳗鱼苗的回归时间，再加上长久以来的过度捕捞，根据日本水产厅的数据，今年渔期的鳗苗捕获量只有3.7吨，是2003年以来的最低水平。'},
        {id:9,sm_class:'4',title:'彩虹之国-南非9',time:'2019-10-25 15:14',intro:'来到2019年，持续第六年的鳗荒也不见好转。由于暖流变化“黑潮大蛇行”影响了鳗鱼苗的回归时间，再加上长久以来的过度捕捞，根据日本水产厅的数据，今年渔期的鳗苗捕获量只有3.7吨，是2003年以来的最低水平。'},
        {id:10,sm_class:'5',title:'彩虹之国-南非10',time:'2019-10-25 15:14',intro:'来到2019年，持续第六年的鳗荒也不见好转。由于暖流变化“黑潮大蛇行”影响了鳗鱼苗的回归时间，再加上长久以来的过度捕捞，根据日本水产厅的数据，今年渔期的鳗苗捕获量只有3.7吨，是2003年以来的最低水平。'}
      ],
      transfer_article:this.article
    };
  },
  props: {
    //接收父组件传递过来的参数方法
    deta: {
      default: function() {
        return '';
      }
    }
  },
  beforeCreate(){
    //实例未创建，调用不到data数据
  },
  created() {
    //实例创建完成后调用,此阶段完成了数据的观测,可调用data数据
    if(this.$route.query.sm_class == undefined){
      this.activeClass = 0
    }else{
      this.activeClass = this.$route.query.sm_class
    }
    this.navActive(this.activeClass)
  },
  methods: {
    //定义方法的地方
    //监听方法click事件等，执行drawFeatures方法
    reload () {
      this.isRouterAlive = false
      this.$nextTick(() => (this.isRouterAlive = true))
    },
    navActive(a){
      //this.activeClass = a;
      if(a == 0){
        this.transfer_article = [].concat(this.article)
      }else{
        let arr = [];
        for(let i = 0;i < this.article.length;i++){
          if(this.article[i].sm_class == a){
            arr.push(this.article[i])
          }
        }
        this.transfer_article = [].concat(arr)
      }
    }
  },

  watch: {
      // transfer_article:function(){
      //   this.reload ()
      // },
      $route(){
        //this.clss = this.$route.query.sm_class
        if(this.$route.query.sm_class == undefined){
          this.activeClass = 0
        }else{
          this.activeClass = this.$route.query.sm_class
        }
      },
      activeClass() {
        this.reload()
        this.navActive(this.activeClass)
      }
  },

  mounted() {
    //页面初始化方法
  }
};
</script>

<style scoped>
.it{
  margin: 20px 0;
  background: #5848af;
  padding: 20px 10px;
  border-radius: 5px;
}
.it-container{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  overflow: hidden;
}

.nav{
  margin-bottom: 20px;
  padding: 5px 20px;
  background: #fafafa;
  border-radius: 3px;
}
.nav a{
  display: inline-block;
  padding: 10px 15px;
  color: #000;
  font-weight: bold;
  border-bottom: 2px solid rgba(255, 0, 0, 0);
  cursor: pointer;
  transition: all .5s;
}
.nav .active,
.nav a:hover{
  color: #f00;
  border-bottom: 2px solid rgba(255, 0, 0, 1);
}

.it-content{
  width: 70%;
}

.it-nav{
  width: 27%;
  position: relative;
  left: 0;
  top: 0;
}
.it-nav div{
  width: 100%;
  background: #fafafa;
  padding: 20px 10px;
  border-radius: 5px;
  margin-bottom: 20px;
}
.it-nav div .nav-title{
  color: #000;
  font-size: 1rem;
  font-weight: bold;
  line-height: 2;
  letter-spacing: 1px;
}
.it-nav div a{
  color: #333;
  font-size: .9rem;
  margin-left: 1.5rem;
  line-height: 1.5;
  transition: all .3s;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  border-bottom:1px solid rgba(255,0,0,0);
}
.it-nav div a:hover{
  color: #f00;
  border-bottom:1px solid rgba(255,0,0,1);
}
@media (max-width:1000px){
  .it-content{
    width: 60%;
  }
  .it-nav{
    width: 37%;
  }
}
@media (max-width:750px){
  .it-content{
    width: 100%;
  }
  .it-nav{
    display: none;
  }
}
</style>



